webpackJsonp([231618293077614],{

/***/ 394:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Mike Herchel","author":"Mike Herchel"}},"markdownRemark":{"id":"/Users/mherchel/sites/herchel-new/src/blog/2018-01-15-css-grid/index.md absPath of file >>> MarkdownRemark","html":"<p>One of the best ways for anyone to learn a topic is to teach that topic. To that end, I‚Äôm giving a presentation for my <a href=\"https://www.meetup.com/Gainesville-Front-End-Dev-Meetup/events/246457734/\">local front-end development meetup</a> this week. In preparation, I‚Äôm writing this CSS cheat sheet / blog.</p>\n<p>Before we start, let‚Äôs recite the <em>CSS Grid Cheat Sheet Creed</em>.</p>\n<blockquote>\n<p>This is my CSS Grid cheat sheet. There are many like it, but this one is mine. My cheat sheet is my best friend. It is my life. I must master it as I must master my life. Without me, my cheat sheet is useless. Without my cheat sheet, I am useless‚Ä¶</p>\n</blockquote>\n<h2>My Perspective</h2>\n<p>I‚Äôve been doing web development for a long time (since 2001), and am familiar with most of the web‚Äôs underlying technologies.</p>\n<p>I‚Äôm a big believer in not adding unneeded complexity, so I typically shy away from traditional grid systems such as <a href=\"http://oddbird.net/susy/\">Susy</a>, <a href=\"https://github.com/at-import/Singularity\">Singularity</a>, and <a href=\"https://getbootstrap.com/docs/4.0/layout/grid/\">Bootstrap Grid</a> ‚Äî <em>unless</em> the design is very grid centric. </p>\n<p>My traditional approach to layout has been set the <code>width</code> to a percentage value ‚Äî lets say <code>94%</code>, and then set the <code>max-width</code> of the container to something like <code>1200px</code> or whatever the design calls for. Then I can layout the internal structure through flexbox or floats, using a combination of percentages, pixels, and the CSS <code>calc()</code> function.</p>\n<p>The beauty of CSS Grid is that it allows us the control that traditional grid systems have, but with no added libraries, CSS files, Sass mixins, or complexity.</p>\n<h2>Tips on getting started with grid</h2>\n<p>CSS Grid is pretty easy once you get started with it, but just like CSS, it gets more complicated the deeper you dive. The good news is that you don‚Äôt need to dive very deep to reach some awesome power. For what it‚Äôs worth, I think Grid is easier to learn than was flexbox.</p>\n<p>As with any new syntax, I highly recommend having (or better yet, creating) a cheat sheet for quick reference. Although I created this blog post, there are many other great resources.</p>\n<ul>\n<li><a href=\"https://css-tricks.com/snippets/css/complete-guide-grid/\">CSS Tricks: A Complete Guide to Grid</a></li>\n<li><a href=\"https://alialaa.github.io/css-grid-cheat-sheet/\">Interactive CSS Grid Playground</a></li>\n<li><a href=\"https://tympanus.net/codrops/css_reference/grid/\">Codrops CSS Grid Reference</a></li>\n</ul>\n<p>I also highly recommend <a href=\"https://twitter.com/rachelandrew\">Rachel Andrew‚Äôs</a> website, <a href=\"https://gridbyexample.com\">Grid by Example</a>, which has a plethora of common use cases, and an <em>excellent</em> free <a href=\"https://gridbyexample.com/video/\">video series</a> to get you up and running.</p>\n<p>Now let‚Äôs get to the meat and potatoes of this post‚Ä¶ the syntax.</p>\n<h2>Grid Container Syntax</h2>\n<p>First, we‚Äôll start with the syntax of the grid container. The markup is pretty basic and looks like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>grid<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>grid-child<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>grid-child<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>grid-child<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>grid-child<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>grid-child<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h3>Initialize CSS Grid</h3>\n<p>We have to initialize the grid in order to use all of these awesome grid properties.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Set up columns</h3>\n<p>This sets up three 200px columns.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> 200px 200px 200px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Set up rows</h3>\n<p>Similarly,<code>grid-template-rows</code> sets up your rows.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">/* Set up three 200px rows */</span>\n  <span class=\"token property\">grid-template-rows</span><span class=\"token punctuation\">:</span> 200px 200px 200px<span class=\"token punctuation\">;</span>\n  <span class=\"token comment\">/* The auto keyword is equal to the maximum height of the content. */</span>\n  <span class=\"token property\">grid-template-rows</span><span class=\"token punctuation\">:</span> auto auto auto<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Shortcut syntax for setting up columns and rows</h3>\n<p>The following will set up four <code>50px</code> rows and three <code>200px</code> columns.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* grid-template: rows / columns; */</span>\n  <span class=\"token property\">grid-template</span><span class=\"token punctuation\">:</span> 50px 50px 50px 50px / 200px 200px 200px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Repeat syntax</h3>\n<p>You can accomplish the same thing by using <em>repeat syntax</em>. This also creates three <code>200px</code> columns.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>3, 200px<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>You can mix and match the regular syntax and repeat syntax. This example creates one 300px column, and three <code>200px</code> columns.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> 300px <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>3, 200px<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Grid Gap</h3>\n<p>Instead of margins, you specify the gap between grid children with the <code>grid-gap</code>, <code>grid-row-gap</code>, and <code>grid-column-gap</code> properties.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-gap</span><span class=\"token punctuation\">:</span> 20px<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* All rows and columns will have 20px gap. */</span>\n  <span class=\"token property\">grid-gap</span><span class=\"token punctuation\">:</span> 20px 40px<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* Columnns will have 20px gap. Rows will have 40px gap. */</span>\n\n  <span class=\"token property\">grid-row-gap</span><span class=\"token punctuation\">:</span> 2vw<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* Now rows will have a 2vw (viewport width units) gap. */</span>\n  <span class=\"token property\">grid-column-gap</span><span class=\"token punctuation\">:</span> 10%<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* Columns will have 10% gap between them. */</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>MinMax syntax</h3>\n<p><code>minmax</code> allows you to set a minimum and maximum width of your columns. Doing so helps you avoid using media queries. You can use <code>minmax</code> inside of <code>repeat</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> 200px <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>3, <span class=\"token function\">minmax</span><span class=\"token punctuation\">(</span>100px, 200px<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>FR unit</h3>\n<p>The ‚Äúfr‚Äù in the <code>fr</code> unit stands for <em>fraction</em>. Grid will create an amount of identical width units equal to the sum of all the <code>fr</code> units in the row, and then it will assign the width of each column based on how many <code>fr</code> units are assigned to it.</p>\n<p>So, if I have a three columns that each have <code>1fr</code> width, they will each be 33.33% wide. If they each have <code>2fr</code>, their width will be the same because they‚Äôre still all equal to each other.</p>\n<p>If the first column is <code>2fr</code> and the next two columns are each <code>1fr</code>, then the first column will take up 50%, and the next two will each take up 25%.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* Each of the three columns will take up 33.33% width. */</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>3, 1fr<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* Each of the three columns will still take up 33.33% width because they're all equal. */</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>3, 2fr<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* The first column will take up 50% of the width, and the other columns will each take up 25% width. */</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> 2fr <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>2, 1fr<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Auto-fill property</h3>\n<p><code>auto-fill</code> automatically adds columns to the grid layout according to the container width. The example below will create 5 <code>200px</code> columns. Note that these columns can be empty if elements don‚Äôt exist to fill them in.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n\n  <span class=\"token property\">width</span><span class=\"token punctuation\">:</span> 1000px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>auto-fill, 200px<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Auto-fill + minmax + fr units = Awesome!</h3>\n<p>The syntax below will automatically create as many 200px columns in the row as possible. But, once it cannot fit any more in, it‚Äôll stretch each column out equally to fill out the entire width of the container! üòé</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>auto-fill, <span class=\"token function\">minmax</span><span class=\"token punctuation\">(</span>200px, 1fr<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Grid-auto-rows and grid-auto-columns</h3>\n<p><code>grid-auto-rows</code> and <code>grid-auto-columns</code> specify the height (track size) of the auto-generated columns and rows. This can be in in any unit including <code>fr</code>. The initial value for these properties is <code>auto</code>, which sets the height equal to the largest content item.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n  \n  <span class=\"token comment\">/* All auto-generated rows will have 30px height */</span>\n  <span class=\"token property\">grid-auto-rows</span><span class=\"token punctuation\">:</span> 30px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Masonry layout using grid</h3>\n<p>If you have grid elements that are taking up multiple columns and/or rows, you can encounter situations where ‚Äúholes‚Äù in the grid appear.</p>\n<p>You can fix this by using <code>grid-auto-flow: dense;</code>. Note that this will cause the elements to appear out of the original source order.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* Enable masonry like layout */</span>\n  <span class=\"token property\">grid-auto-flow</span><span class=\"token punctuation\">:</span> dense<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h2>Grid Child Syntax</h2>\n<p>The following properties apply to children of grid containers.</p>\n<h3>Align tracks within a grid</h3>\n<p>A track is either a column or a row. What if the grid is wider or higher than your grid tracks? How do you control where the tracks go?</p>\n<p>You can use our old flexbox friends <code>justify-content</code> and <code>align-content</code>! The values to use are <code>start</code>, <code>center</code>, and <code>end</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">width</span><span class=\"token punctuation\">:</span> 1000px<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* Explicit width */</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>3, 300px<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\">/* Note that there'll be an extra 100px left over */</span>\n  <span class=\"token property\">justify-content</span><span class=\"token punctuation\">:</span> end<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* This will align the columns to the right. Can also be start (default) or center */</span>\n  <span class=\"token property\">align-items</span><span class=\"token punctuation\">:</span> end<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* If I had extra vertical space, this would align the rows to the bottom of the grid. */</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>You can also use our flexbox friends <code>space-between</code> and <code>space-around</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">justify-content</span><span class=\"token punctuation\">:</span> space-between<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* Inserts an equal amount of space between the columns.*/</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Start a child on a specific column</h3>\n<p>This grid child will start on the second column.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-column-start</span><span class=\"token punctuation\">:</span> 2<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Have a child span multiple columns</h3>\n<p>The following rule will have the child take up two columns</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">/* Grid child element will always take up two columns */</span>\n  <span class=\"token property\">grid-column-end</span><span class=\"token punctuation\">:</span> span 2<span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* Grid child element will start on column two, and go to column 4 */</span>\n  <span class=\"token property\">grid-column-start</span><span class=\"token punctuation\">:</span> 2<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-column-end</span><span class=\"token punctuation\">:</span> span 2<span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* You can also specify the exact end column */</span>\n  <span class=\"token property\">grid-column-start</span><span class=\"token punctuation\">:</span> 2<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-column-end</span><span class=\"token punctuation\">:</span> 4<span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* Shorthand for this specifying the ending column */</span>\n  <span class=\"token property\">grid-column</span><span class=\"token punctuation\">:</span> 2 / 4<span class=\"token punctuation\">;</span> \n\n  <span class=\"token comment\">/* Shorthand specifying that element always spans 2 columns */</span>\n  <span class=\"token property\">grid-column</span><span class=\"token punctuation\">:</span> 2 / span 2<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Have a child span multiple <em>rows</em></h3>\n<p>Similarly, the following rule will have the child take up two <em>rows</em></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-row-end</span><span class=\"token punctuation\">:</span> span 2<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Always put a child into a certain row</h3>\n<p>I can see this being is useful for advertisements.</p>\n<p>Note that you can reference rows from the end by using negative integers. So <code>grid-row: -2</code> will reference the second to last row.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">/* This particular element will always be in the second row */</span>\n  <span class=\"token property\">grid-row</span><span class=\"token punctuation\">:</span> 2<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Have child element take up the full width of the row</h3>\n<p>The following snippet will place the <code>.grid-child</code> element into the third row and have it span the entire width of the row.</p>\n<p>Note that I‚Äôm setting <code>grid-column-end</code> to <code>-1</code>. Negative numbers selects the columns (or rows) from the end. So <code>-1</code> will always select the last column (or row).</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-row</span><span class=\"token punctuation\">:</span> 3<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* Place the element into the third row */</span>\n  <span class=\"token property\">grid-column-start</span><span class=\"token punctuation\">:</span> 1<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* Start the element in the first column */</span>\n  <span class=\"token property\">grid-column-end</span><span class=\"token punctuation\">:</span> -1<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* End the column at the last column. Remember that you can reference\n                          columns and rows from the end by using negative integers */</span>\n  <span class=\"token property\">grid-column</span><span class=\"token punctuation\">:</span> 1 / -1<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* Shorthand syntax for grid-column-start and grid-column-end */</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Place a child in a certain column and have it span multiple columns</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">/* Shorthand syntax for grid-column-start and grid-column-end.\n     This will start in column 2, and span 2 more columns. */</span>\n  <span class=\"token property\">grid-column</span><span class=\"token punctuation\">:</span> 2 / span 2<span class=\"token punctuation\">;</span> \n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h2>Helpful syntax</h2>\n<p>Because this is web development, there are many approaches to accomplish the same thing. Here are some cool helpful syntax types.</p>\n<h3>Named lines</h3>\n<p>You can use <em>names</em> instead of numbers for your lines. Note that you‚Äôre not naming your regions, you‚Äôre naming the lines <em>around the regions</em>. Then when you assign a child element, you assign it by <em>name</em> instead of <em>number</em>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* The line between the first and second column is named \"content-start\",\n     and the line at the end is called \"content-end.\" */</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> 1fr [content-start] 3fr [content-end]<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-template-rows</span><span class=\"token punctuation\">:</span> auto [content-start] auto [content-end]<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">/* Now instead of using numbered column names, I can used the named column names. */</span>\n<span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-column</span><span class=\"token punctuation\">:</span> content-start<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-row</span><span class=\"token punctuation\">:</span> content_start<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Named lines with media queries == ü§ò</h3>\n<p>The cool thing about named lines is that it makes responsive web design <em>much simpler</em>. When defining placement on the <code>.grid-child</code>, you do not need to add media queries for this element ‚Äî it will automatically follow the named lines. </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token comment\">/* Define our grid at small widths. Note that we can name the lines to the\n   left and right of the grid. */</span>\n<span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> [content-start] 1fr [content-end]<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">/* Now, let's change the layout of the grid in a media query. */</span>\n<span class=\"token atrule\"><span class=\"token rule\">@media</span> <span class=\"token punctuation\">(</span><span class=\"token property\">min-width</span><span class=\"token punctuation\">:</span> 700px<span class=\"token punctuation\">)</span></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">grid-template-columns</span><span class=\"token punctuation\">:</span> 1fr [content-start] 3fr [content-end]<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">/* And now, we only need to set the columns on our grid child once. \n   This child will follow the named lines for the media query. */</span>\n<span class=\"token selector\">.grid-child</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-column</span><span class=\"token punctuation\">:</span> content-start<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Named Areas</h3>\n<p>You can name your grid children, and then place them <em>ascii-art style</em> within a new CSS property called <code>grid-template-areas</code>. In the example below, I‚Äôm using semantic element types to illustrate where we want the content to be placed.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">header</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-area</span><span class=\"token punctuation\">:</span> header<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* You can name these whever you want */</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">aside</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-area</span><span class=\"token punctuation\">:</span> sidebar<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">main</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-area</span><span class=\"token punctuation\">:</span> content<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">footer</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">grid-area</span><span class=\"token punctuation\">:</span> footer<span class=\"token punctuation\">;</span><span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>Once the areas are named, we line em up in an ascii-art like grid.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* This will stack them in a vertical column for a mobile layout. */</span>\n  <span class=\"token property\">grid-template-areas</span><span class=\"token punctuation\">:</span> \n    <span class=\"token string\">'header'</span>\n    <span class=\"token string\">'content'</span>\n    <span class=\"token string\">'sidebar'</span>\n    <span class=\"token string\">'footer'</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token atrule\"><span class=\"token rule\">@media</span> <span class=\"token punctuation\">(</span><span class=\"token property\">min-width</span><span class=\"token punctuation\">:</span> 700px<span class=\"token punctuation\">)</span></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">/* Now we do a traditional grid area. */</span>\n    <span class=\"token property\">grid-template-areas</span><span class=\"token punctuation\">:</span> \n      <span class=\"token string\">'header  header'</span>\n      <span class=\"token string\">'sidebar content'</span>\n      <span class=\"token string\">'footer  footer'</span><span class=\"token punctuation\">;</span>\n    <span class=\"token comment\">/* Note that we put header twice because it spans two columns.\n       On the next row, we have sidebar and column taking up the row.\n       And on the third row, we have footer twice.\n\n       You can also use a dot (.) to indicate a blank area. */</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n\n<span class=\"token atrule\"><span class=\"token rule\">@media</span> <span class=\"token punctuation\">(</span><span class=\"token property\">min-width</span><span class=\"token punctuation\">:</span> 1000px<span class=\"token punctuation\">)</span></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">/* Change around the regions at a larger viewport width. */</span>\n    <span class=\"token property\">grid-template-areas</span><span class=\"token punctuation\">:</span> \n      <span class=\"token string\">'sidebar header header'</span>\n      <span class=\"token string\">'sidebar content content'</span>\n      <span class=\"token string\">'.       footer footer'</span><span class=\"token punctuation\">;</span>\n    <span class=\"token comment\">/* We're having the Sidebar take up the entire first column. \n       The first column of the last row will be empty. */</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>Magic Lines</h3>\n<p>When defining named areas, CSS Grid will automatically generate <code>-start</code> and <code>-end</code> lines for each region!</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">/* Lets create a grid using named areas */</span>\n  <span class=\"token property\">grid-template-areas</span><span class=\"token punctuation\">:</span> \n    <span class=\"token string\">'sidebar header header'</span>\n    <span class=\"token string\">'sidebar content content'</span>\n    <span class=\"token string\">'.       footer footer'</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>So for the grid above, the browser will automatically generate <code>sidebar-start</code>, <code>sidebar-end</code>, <code>header-start</code>, <code>header-end</code>, <code>content-start</code>, <code>content-end</code>, <code>footer-start</code>, and <code>footer-end</code> named lines! </p>\n<p>Pretty neat!</p>\n<p>This also works in reverse, where if you name your lines with the pattern of <em>&#x3C;region>-start</em> and <em>&#x3C;region>-end</em>, it will automatically name the <code>region</code> area!</p>\n<h3>Grid-template shorthand</h3>\n<p>We already know that we can define your track widths/heights with shortcut syntax in the <code>grid-template</code> area:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n\n  <span class=\"token comment\">/* grid-template: rows / columns; \n     Create four 50px rows, and three 200px columns. */</span>\n  <span class=\"token property\">grid-template</span><span class=\"token punctuation\">:</span> 50px 50px 50px 50px / 200px 200px 200px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>But we can also define your <em>grid areas</em> and column widths / row heights all in one property.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.grid</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> grid<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">grid-template</span><span class=\"token punctuation\">:</span> <span class=\"token string\">'head head head head'</span> 200px\n                 <span class=\"token string\">'side1 main main side2'</span> 300px / <span class=\"token function\">repeat</span><span class=\"token punctuation\">(</span>3, 1fr<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>This syntax is confusing as hell if you haven‚Äôt seen it. </p>\n<p>What‚Äôs going on is the named areas are being mapped to the grid template. After the named areas are placed, the track widths are defined. </p>\n<p>So on the first line, after the <code>head</code> area is set to span 4 columns, the row height is set to <code>200px</code>. On the second line, the <code>side1</code> , <code>main</code>, and <code>side2</code> regions are placed. After that, the row height is set to <code>300px</code>, and the columns are set to three <code>1fr</code> columns.</p>\n<p>For more information, see this video from Rachel Andrew‚Äôs <a href=\"https://gridbyexample.com\">Grid by Example series</a>:</p>\n<p><div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 50%; position: relative; height: 0; overflow: hidden;margin-bottom: 1.0725rem\"\n          >\n            <div><iframe src=\"https://www.youtube.com/embed/aqLifuKpfHU\" style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe></div>\n          </div>\n          </div></p>\n<h2>Developer Tooling for CSS Grid</h2>\n<p>Both Chrome and Firefox have developer tools support for CSS Grid. When hovering your mouse above the grid container, both browser‚Äôs developer tools will highlight the track and the lines of the grid. Firefox‚Äôs developer tools have a leg up, though, because you can toggle on and off the highlighting by clicking on a <em>grid icon</em> that appears next to the <code>display: grid;</code> declaration. With Chrome‚Äôs developer tools, you have to keep your mouse highlighted above the element in order to view the tracks and lines.</p>\n<figure border=\"\">\n \n  <a class=\"gatsby-resp-image-link\" href=\"/static/firefox-grid-devtools-16dad2a34be0720e0a8aedb1d563d97b-e409e.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 59.297789336801046%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Firefox Grid Devtools\" title=\"\" src=\"/static/firefox-grid-devtools-16dad2a34be0720e0a8aedb1d563d97b-42603.png\" srcset=\"/static/firefox-grid-devtools-16dad2a34be0720e0a8aedb1d563d97b-f931c.png 200w,\n/static/firefox-grid-devtools-16dad2a34be0720e0a8aedb1d563d97b-e8031.png 400w,\n/static/firefox-grid-devtools-16dad2a34be0720e0a8aedb1d563d97b-42603.png 800w,\n/static/firefox-grid-devtools-16dad2a34be0720e0a8aedb1d563d97b-5b8b9.png 1200w,\n/static/firefox-grid-devtools-16dad2a34be0720e0a8aedb1d563d97b-e409e.png 1538w\" sizes=\"(max-width: 800px) 100vw, 800px\">\n    </span>\n  </span>\n  \n  </a>\n    \n <figcaption>Firefox&apos;s Grid Developer Tools showing an example from <a href=\"https://gridbyexample.com\">Grid by Example</a></figcaption>\n</figure>\n<p>That being said, the developer tools could be better. When hovering the mouse above named areas, it would be nice if the areas would be highlighted. The same goes for named lines ‚Äî hovering over the named lines declaration should highlight the appropriate line. I filed some feature requests for both <a href=\"https://bugs.chromium.org/p/chromium/issues/detail?id=801897\">Chrome</a> and <a href=\"https://bugzilla.mozilla.org/show_bug.cgi?id=1430473\">Firefox</a> to get these features implemented.</p>\n<p>In addition when using named areas, the entire value is placed on one line ‚Äî totally negating the <em>ascii-art</em> matrix where you lined up the grid. DevTools should preserve line breaks and white space. I also filed featured requests these at: <a href=\"https://bugs.chromium.org/p/chromium/issues/detail?id=801896\">Chrome</a> and <a href=\"https://bugzilla.mozilla.org/show_bug.cgi?id=1430471\">Firefox</a>.</p>\n<p>Furthermore, it would be <em>really</em> useful if the browsers integrated a grid builder, similar to what they have for <a href=\"https://brettdewoody.com/using-chromes-cubic-bezier-editor/\">bezier-curve editor</a>, or color-picker within devtools.</p>\n<h2>Should I be using CSS Grid now?</h2>\n<p> As long as you‚Äôre not supporting Internet Explorer 11, then the answer is a resounding‚Ä¶ <em>sure</em>! Whether to support IE11 is a decision that your team has to make after looking at your website‚Äôs visitor logs. </p>\n<p> In the United States, over 85% of browsers support Grid, although your website‚Äôs demographic might be dramatically different. Keep in mind the percentage of browsers supporting Grid is only going to continue to increase as time goes on. </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/can-i-use-css-grid-3fbb363a0d9071d51739f0a853c84170-59a9f.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 44.90674318507891%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Browser Support for CSS Grid\"\n        title=\"\"\n        src=\"/static/can-i-use-css-grid-3fbb363a0d9071d51739f0a853c84170-42603.png\"\n        srcset=\"/static/can-i-use-css-grid-3fbb363a0d9071d51739f0a853c84170-f931c.png 200w,\n/static/can-i-use-css-grid-3fbb363a0d9071d51739f0a853c84170-e8031.png 400w,\n/static/can-i-use-css-grid-3fbb363a0d9071d51739f0a853c84170-42603.png 800w,\n/static/can-i-use-css-grid-3fbb363a0d9071d51739f0a853c84170-5b8b9.png 1200w,\n/static/can-i-use-css-grid-3fbb363a0d9071d51739f0a853c84170-59a9f.png 1394w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2>Should I rewrite my current application to use CSS Grid?</h2>\n<p>If you‚Äôre current web app is working perfectly, should you rewrite? </p>\n<p>The benefits of doing so would be a simpler code base ‚Äî you‚Äôre able to rip out your current grid system, and replace your current layout system(s).</p>\n<p>The downsides of rewriting are lesser browser support, the probability of introduced bugs, not to mention the hours spent for the rewriting and for the quality control.</p>\n<p>In my opinion, <em>if it ain‚Äôt broke, don‚Äôt fix it</em>. But, only you know your web app, and can make the correct decision.</p>\n<p><em>Thanks to <a href=\"https://twitter.com/wesruv\">Wes Ruvalcaba</a> and <a href=\"https://twitter.com/larkintuckerllc\">John Tucker</a> for helping to review this post.</em></p>","excerpt":"One of the best ways for anyone to learn a topic is to teach that topic. To that end, I‚Äôm giving a presentation for my  local front-end‚Ä¶","frontmatter":{"title":"Getting Up and Running with CSS Grid","date":"January 15, 2018","author":""}}},"pathContext":{"slug":"/2018-01-15-css-grid/"}}

/***/ })

});


